import {
  Component,
  OnInit,
  Input,
  ViewChild,
  ElementRef,
  Injector,
  ComponentFactoryResolver,
  Renderer2,
  ApplicationRef,
  EventEmitter,
  Output,
  OnDestroy,
  OnChanges
} from '@angular/core';

import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CaptionComponent } from '../../components/caption/caption.component';
import { Observable, Subject } from 'rxjs';
import { LrsManagerService } from '../../services/lrs-manager.service';
import { TokenManagerService } from '../../services/token-manager.service';
import { DeviceDetectorService } from 'ngx-device-detector';

declare let videojs;
declare let mux;
@Component({
  selector: 'tce-video-player',
  templateUrl: './video-player.component.html',
  styleUrls: ['./video-player.component.scss']
})
export class VideoPlayerComponent implements OnInit, OnDestroy {
  // --------------------------------------------------------------------------------------------------------------------
  public updateMarkers: Subject<any> = new Subject<any>();

  // Inputs from parents who using this library
  /*
        External triggers to control video
        usecase 1. pause the video on an app activity
        usecase 2. load the video at a specific cue point
        usecase 3. reset player settings on new video url

    */
  @Input() play: Observable<void>; // play video from outside of player
  @Input() pause: Observable<void>; // pause video from outside of player
  @Input() setTime: Observable<void>; // set Current time video from outside of player
  @Input() updateCue: Observable<void>; // update cue points if its position changed or added new cue from outside of player
  @Input() resetPlayer: Observable<void>; // Reset player

  /*
       Trigger to parent container

    */
  @Output() cuePointTrigger = new EventEmitter(); // this event will be trigger when player hit the cue point
  @Output() cuePointClicked = new EventEmitter(); // this event will be trigger if cue point clicked by user
  @Output() endCuePointTrigger = new EventEmitter(); // this event will be trigger if cue point clicked by user
  @Output() timeChange = new EventEmitter(); // this will emit when time change of player
  @Output() videoComplete = new EventEmitter(); // On video complete
  @Output() videoLoaded = new EventEmitter(); // On video loaded
  @Output() videoSeeked = new EventEmitter(); // On video seeked
  @Output() videoDuration = new EventEmitter(); // On video seeked

  /*
       Subscriptions references , these will be destroyed once unsubscribed

    */
  // Subscribe and stored in this veriable to unsubscrobe when destroy
  private playSubscribe: any;
  private pauseSubscribe: any;
  private setTimeSubscribe: any;
  private updateCueSubscribe: any;
  private resetPlayerSubscribe: any;
  // --------------------------------------------------------------------------------------------------------------------

  player: any;
  track: any;
  source: any;
  videojsoptions: any;
  playerloader: any;
  customPlayer: any;
  stateToggle = false;
  videoTrack: any;
  addcuepoint = false;
  CreateCuePoint = false;
  previewstatus = false;
  currenttime: any;
  profileForm: FormGroup;
  playFlag = false;
  pauseFlag = false;
  blankFlag = false;
  flag = true;
  @Input() videoPlayerdata: any;
  @ViewChild('video', { static: true })
  public video: ElementRef;

  constructor(
    private resolver: ComponentFactoryResolver,
    private renderer: Renderer2,
    private injector: Injector,
    private app: ApplicationRef,
    private formBuilder: FormBuilder,
    private lrs: LrsManagerService,
    private tokenMgr: TokenManagerService,
    public deviceService: DeviceDetectorService
  ) {
    this.profileForm = this.formBuilder.group({
      fromduration: ['', Validators.required],
      toduration: ['']
    });
  }

  ngOnInit() {
    console.log(this.videoPlayerdata);
    if (this.deviceService.isMobile()) {
      console.log(this.deviceService.isMobile());
      this.videojsoptions = {
        controls: true,
        autoplay: false,
        preload: 'auto',
        techOrder: ['html5'],
        controlBar: {
          volumePanel: false,
          fullscreenToggle: false,
          pictureInPictureToggle: false
        }
      };
    } else {
      this.videojsoptions = {
        controls: true,
        autoplay: false,
        preload: 'auto',
        techOrder: ['html5'],
        controlBar: {
          fullscreenToggle: false,
          pictureInPictureToggle: false
        }
      };
    }
    if (this.videoPlayerdata.interactiveVideo.video.files[0]) {
      this.getToken(this.videoPlayerdata.interactiveVideo.video.files[0]);
     // this.loadVideo(this.videoPlayerdata.interactiveVideo.video.files[0], '');
    }
    this.subscribeEvents();
  }
  // ------------------------------------------------------------------------------------------------------------
  /*
    subscribe this event so parent can manage some property of player like play,
     pause, jump to current time, update marker
  */
  subscribeEvents() {
    try {
      this.playSubscribe = this.play.subscribe(e => {
        this.playerloader.play();
      });
      this.pauseSubscribe = this.pause.subscribe(e => {
        this.playerloader.pause();
      });
      this.setTimeSubscribe = this.setTime.subscribe(e => {
        this.playerloader.currentTime(e);
      });
      this.updateCueSubscribe = this.updateCue.subscribe(e => {
        this.updateMarkers.next(e);
        // Update marker here if it change outside of player
      });
      this.resetPlayerSubscribe = this.resetPlayer.subscribe(e => {
        this.playerloader.reset();
      });
    } catch (error) {
      console.log(error);
    }
  }
  initEvents() {
    this.playerloader.on('loadedmetadata', () => {
      this.addcomponent();
      // this.lrs.initLrs();
    });

    this.playerloader.on('play', () => {
      this.addcuepoint = false;
      this.CreateCuePoint = false;
      this.pauseFlag = false;
      this.playFlag = false;
      this.blankFlag = true;
    });

    this.playerloader.on('pause', () => {
      this.addcuepoint = true;
      this.playFlag = false;
      this.pauseFlag = false;
      this.blankFlag = true;
      this.currenttime = this.playerloader.currentTime();
    });

    this.playerloader.on('timeupdate', () => {
      this.timeChange.emit(Math.round(this.playerloader.currentTime()));
    });

    this.playerloader.on('ended', () => {
      this.videoComplete.emit();
    });

    this.playerloader.on('loadeddata', () => {
      this.videoLoaded.emit();
      this.videoDuration.emit(this.playerloader.duration());
    });

    this.playerloader.on('seeked', () => {
      this.videoSeeked.emit(Math.round(this.playerloader.currentTime()));
    });
    this.playerloader.on('mouseout', function(){
     // controlBar.addClass('vjs-fade-out'); 
     console.log('mouseout');
    });
    
    this.playerloader.on('tap', function(){ 
     // controlBar.removeClass('vjs-fade-out'); 
     console.log('tap');
    });
  }

  getToken(data) {
    const aclArr = data.path.split('/');
    console.log('aclArr', aclArr);
    aclArr.splice(-2, 2);
    console.log('aclArr', aclArr);
    const aclPath = aclArr.join('/');
    console.log('aclPath', aclPath);
    this.tokenMgr.getNewToken('/assets/*', 900, null).subscribe(
      resp => {
        if (resp.body.status === 'success') {
          this.loadVideo(
            this.videoPlayerdata.interactiveVideo.video.files[0],
            resp.body.token
          );
        }
      },
      error => {
        console.log('error', error);
      }
    );
  }

  //------------------------------------------------------------------------------------------------------------
  loadVideo(data, token) {
    let newtrack: any;
    this.playerloader = videojs(this.video.nativeElement, this.videojsoptions);
    console.log(this.playerloader);
    this.playerloader.on('ready', () => {
      this.playerloader.src({
        src: data.path + '?hdnts=' + token,
        type: "application/x-mpegURL"
      });
      this.source = document.createElement('source');
      this.source.src = data.path + '?hdnts=' + token;
      this.source.type = "application/x-mpegURL";
      this.video.nativeElement.appendChild(this.source);
      this.videoTrack = this.videoPlayerdata.interactiveVideo.video.textTracks.videoTrack;
      if (this.videoTrack) {
        for (let index = 0; index < this.videoTrack.length; index++) {
          this.track = document.createElement('track');
          this.track.kind = this.videoTrack[index].kind;
          this.track.label = this.videoTrack[index].label;
          this.track.srclang = this.videoTrack[index].srcLang;
          (this.track.src = this.videoTrack[index].video_track),
            (this.track.default = true);
          newtrack += this.track;
        }
        this.video.nativeElement.appendChild(this.track);
      }
      this.initEvents();
    });
  }

  addcomponent() {
    const playheadWell = document.getElementsByClassName(
      'vjs-progress-control vjs-control'
    )[0];
    const factory = this.resolver.resolveComponentFactory(CaptionComponent);
    const elem = this.renderer.createElement('div');
    this.renderer.appendChild(playheadWell, elem);
    const ref = factory.create(this.injector, [], elem);
    ref.instance.videojs = this.playerloader;
    ref.instance.currentvideodata = this.videoPlayerdata.interactiveVideo.assets.interactions;
    ref.instance.updateMarker = this.updateMarkers.asObservable();
    ref.instance.videoduration = this.playerloader.duration();
    let data = {
      id: this.playerloader.id_,
      student_id: 123,
      title: this.videoPlayerdata.interactiveVideo.video.files[0].copyright
        .title,
      duration: this.playerloader.duration()
    };
    // this.lrs.InitVideoMux(this.playerloader.id_,data)
    ref.instance.cuePointTrigger.subscribe(val =>
      this.cuePointTrigger.emit(val)
    );
    ref.instance.cuePointClicked.subscribe(val =>
      this.cuePointClicked.emit(val)
    );
    ref.instance.endCuePointTrigger.subscribe(() => {
      this.endCuePointTrigger.emit();
    });
    this.app.attachView(ref.hostView);
  }

  customplay() {
    this.stateToggle = true;
    this.playerloader.play().then(
      () => {},
      err => {}
    );
  }

  togglePause() {
    this.playerloader = videojs(this.video.nativeElement, this.videojsoptions);
    this.playerloader.userActive(false);
    if (this.playerloader.paused()) {
      this.playerloader.play();
      this.playFlag = false;
      this.pauseFlag = false;
      this.blankFlag = true;
    } else {
      var self = this;
      this.flag = ! this.flag;
      this.playFlag = false;
      this.pauseFlag = false;
      this.blankFlag = true;
      this.playerloader.pause();
      this.playerloader.controls(false);
     // this.playerloader.userActive(true);
        // if ( self.flag){ 
        // self.playerloader.pause();
        // self.playFlag = true;
        // self.pauseFlag = false;
        // } else {
         
        //   setTimeout(function() {
        //     self.playFlag = false;
        //     self.pauseFlag = true;
            
        //     self.playerloader.userActive(false);
        //   }, 3000);
        // }
    }
  }



  blankPlayer(){
    this.playerloader = videojs(this.video.nativeElement, this.videojsoptions);
    this.playerloader.controls(true);
    this.blankFlag = false;
    if (this.playerloader.paused()) {
      this.playFlag = true;
      this.playerloader.userActive(true);
    } else {
      this.pauseFlag = true;
      this.playerloader.userActive(true);
    }
    var self = this;
    setTimeout(function() {
      self.playFlag = false;
      self.pauseFlag = false;
      self.blankFlag = true;
      self.playerloader.controls(true);
      self.playerloader.userActive(false);
    }, 3000);

  }
  custompause() {
    this.stateToggle = false;
    this.addcuepoint = true;
    this.playerloader.pause();
  }

  onSubmit() {
    let arraydata = {};
    arraydata = {
      action: {
        library: '',
        params: {},
        x: 0,
        y: 0,
        duration: {
          from: this.profileForm.value.fromduration,
          to: this.profileForm.value.toduration
        },
        pause: false,
        label: '',
        width: 0,
        height: 0,
        displayType: '',
        visuals: {
          backgroundColor: '',
          boxShadow: true
        },
        goto: {
          url: {
            protocol: ''
          },
          visualize: false,
          type: ''
        },
        buttonOnMobile: true
      }
    };
    this.videoPlayerdata.interactiveVideo.assets.interactions.push(arraydata);
  }

  get code() {
    if (this.videoPlayerdata.interactiveVideo.assets.interactions) {
      return JSON.parse(
        JSON.stringify(
          this.videoPlayerdata.interactiveVideo.assets.interactions
        )
      );
    }
  }

  set code(v) {
    try {
      if (this.videoPlayerdata.interactiveVideo.assets.interactions) {
        this.videoPlayerdata.interactiveVideo.assets.interactions = JSON.parse(
          v
        );
      }
    } catch (e) {
      console.log('error occored while you were typing the JSON', e);
    }
  }

  showCuepointForm() {
    this.CreateCuePoint = true;
  }

  preview() {
    this.previewstatus = true;
  }

  edit() {
    this.previewstatus = false;
  }

  ngOnDestroy() {
    //every subscription will be removed on this lib destroy
    try {
      this.playerloader.dispose();
      this.playSubscribe.unsubscribe();
      this.pauseSubscribe.unsubscribe();
      this.setTimeSubscribe.unsubscribe();
      this.updateCueSubscribe.unsubscribe();
      this.resetPlayerSubscribe.unsubscribe();
    } catch (error) {}
  }
}
