import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

@Injectable({
  // tslint:disable-next-line:indent
  providedIn: 'root'
})
export class TokenManagerService {
  generateAkamaiToken = "https://api.examchamp.io/video/prod/generateAkamaiToken";
  akamai = "b3c77ba342fe08afbbbbb9b56dcf92cf";
  constructor(private http: HttpClient) {}

  getNewToken(acl: string, windowSeconds: number, url: string) {
    const obj = { acl, windowSeconds, url, key: this.akamai };
    return this.serviceCall(this.generateAkamaiToken, obj);
  }

  serviceCall(url, obj) {
    return this.http.post<any>(url, obj, {}).pipe(
      map(user => {
        // store user details and jwt token in local storage to keep user logged in between page refreshes
        localStorage.setItem('currentUser', JSON.stringify(user));
        return user;
      })
    );
  }
}
