import { Injectable } from '@angular/core';
// import { UserService } from '../../../../../apps/core-app/src/app/core/services/user.service';

declare let videojs;
declare let mux;
declare const ADL: any;
@Injectable({
  providedIn: 'root'
})
export class LrsManagerService {
  userDetails;
  constructor() {
    // this.userDetails = this.user.getUserData();
  }

  initLrs() {
    // This line limits the number of logs in your console...
    ADL.XAPIWrapper.log.debug = true;

    const activateXAPI = true;
    console.log('this.userDetails LrsManagerService', this.userDetails);
    // ADL LRS Configuration - Basic Auth for demo purposes

    if (activateXAPI) {
      // && ADL.XAPIWrapper.lrs.auth == undefined) {
      console.log('activateXAPI', activateXAPI);
      const conf = {
        endpoint: 'http://localhost:4001/demo/xapi/', // LRS URL
        auth: 'Basic ' + window.btoa('santosh@admin:password'), // LRS client details
        actor: JSON.stringify({
          mbox: 'mailto:' + this.userDetails.emailId,
          name: this.userDetails.firstname,
          objectType: 'Agent'
        })
      };
      console.log('conf', conf);
      ADL.XAPIWrapper.changeConfig(conf);

      if (ADL.XAPIWrapper.testConfig()) {
        ADL.XAPIVideoJS('video'); // Register <video> tag with xAPI-video.js
      } else {
      }
    }
  }

  InitVideoMux(id, data) {
    //console.log(data);
    // Record the player init time
    var playerInitTime = Date.now();
    //console.log(playerInitTime);
    let player = videojs(id);
    //console.log(player);
    player.mux({
      debug: true,
      data: {
        env_key: 'omna7ninob6ktc0cce49bejrr', // required

        // Site Metadata
        viewer_user_id: data.student_id, // ex: '12345'

        // Player Metadata
        player_name: 'video.js', // ex: 'My Main Player'
        player_version: '7.6.6', // ex: '1.0.0'
        player_init_time: playerInitTime, // ex: 1451606400000

        // Video Metadata (cleared with 'videochange' event)
        video_id: data.id, // ex: 'abcd123'
        video_title: data.title, // ex: 'My Great Video'
        video_series: 'ECE', // ex: 'Weekly Great Videos'
        video_duration: data.duration, // in milliseconds, ex: 120000
        video_stream_type: 'on-demand', // 'live' or 'on-demand'
        video_cdn: 'Akamai' // ex: 'Fastly', 'Akamai'
      }
    });
  }
}
